import moment from 'moment'
import { mapGetters } from 'vuex'
import { getLocalStorage } from '@/assets/js/localStorage'
import { birthByIdNum, diffDate } from '@/assets/js/date'
import { sexhByIdNum } from '@/assets/js/base'
export const getProductDetail = {
  data () {
    return {
      tbShow: false,
      planIdArr: [],
      birthday: '',
      bbxrName: '',
      currentPlanIndex: 0,
      addInsuredShow: false,
      insuredShow: false,
      plan_choice: '',
      totalPrice: '',
      calculationFactors: [],
      insuredList: {},
      amemdPlanId: '',
      amendBbxrName: '',
      plans: [],
      planCons: [],
      upImg: {
        'background-image':
        'url(' + require('@/assets/images/icon_up.png') + ')'
      },
      downImg: {
        'background-image':
        'url(' + require('@/assets/images/icon_down.png') + ')'
      },
      startDatefault: moment(new Date()).add(1, 'days').format('YYYY-MM-DD')
    }
  },
  mounted () {
  /*  this.getCommon()//判断是否有数据 没有不显示 */

    if (getLocalStorage('token')) {
      this.getInsurdList()
      this.birthday = birthByIdNum(getLocalStorage('userInfo').id_num)
      this.insured.sex = sexhByIdNum(getLocalStorage('userInfo').id_num)
      this.insured.age = diffDate(this.birthday, this.startDate)
    }
    if (this.$route.query.fromApplicant) {
      this.tbShow = true
    }
    if (!this.$route.query.orderSn) {
      this.getPlans()
      this.getCalculationFactors()
    }
    if (this.$route.query.orderSn) {
      this.tbShow = true
      this.getCalculationFactors().then(res => {
        if (res) {
          this.amendOrder().then(res => {
            if (res) {
              this.getPlans()
            }
          })
        }
      })
    }
  },
  methods: {
    amendOrder () {
      return new Promise((resolve, reject) => {
        let postData = {
          orderSn: this.$route.query.orderSn
        }
        this.$api.product.amendOrder(postData).then(res => {
          if (res.code === 1) {
            // this.startDate = moment(res.data.policy_effective_date).format('YYYY-MM-DD')
            // this.endDate = moment(res.data.policy_expiration_date).format('YYYY-MM-DD')
            this.amemdPlanId = res.data.plan_id
            this.amendBbxrName = res.data.relation_name
            this.totalPrice = res.data.price
            this.plan_choice = res.data.plan_id
            resolve(res.data)
          } else {
            this.$toast.center(res.message)
          }
        })
      })
    },
    getPlans () {

      let postData = {
        productId: this.$route.query.productId
      }
      this.$api.product.getPlans(postData).then(res => {
        if (res.code === 1) {
          for (let i = 0; i < res.data.length; i++) {
            if (res.data[i].id === this.amemdPlanId) {
              this.currentPlanIndex = i
            }
            this.plans = this.plans.concat(res.data[i])
            this.planCons[i] = this.plans[i].plan_details
            this.planIdArr = this.planIdArr.concat(res.data[i].id)
          }
        }
      })
    },
    changePlan (index) {
      this.currentPlanIndex = index
      this.plan_choice = this.plans[index].id
    },
    seletcInsured (item) {
      this.bbxrName = item.name
      this.insuredShow = false
      this.insured.relationId = item.id
    },
    getInsurdList () {
      let postData = {
        page: 1,
        limit: 1000
      }
      this.$api.personCenter.getInsured(postData).then(res => {
        if (res.code === 1) {
          this.insuredList = res.data.data
          this.insuredList.forEach((item, index) => {
            if (item.type === 6) {
              this.insured.relationId = item.id
            }
          })
        }
      })
    }
  },
  computed: {
    insuredName () {
      if (this.bbxrName) {
        return this.bbxrName
      } else if (this.$route.query.orderSn) {
        return this.amendBbxrName
      } else {
        return this.userInfo.name
      }
    },
    ...mapGetters([
      'userInfo'
    ])
  }
}
