<template>
	<div class="product-detail">
		<div class="product-calculation" v-if="tbShow">
			<p class="close" @click="tbShow=false"><img src="../../../assets/images/close.png" alt=""></p>
			<div class="content">
				<div class="layer ">
					<div>投保人</div>
					<div>{{userInfo.name}}</div>
				</div>
				<div class="layer ">
					<div>为谁投保</div>
					<div>{{insuredName}}</div>
					<!--<div class=" icon_insure" @click="insuredShow=true">{{insuredName}}</div>-->
				</div>
				<div class="layer ">
					<div>保险期间</div>
					<div>1年</div>
				</div>
				<div class="layer ">
					<div>生效日期</div>
					<div class="date"><span>{{startDate}}</span></div>
				</div>
				<div class="layer ">
					<div>终止日期</div>
					<div>{{endDate}}</div>
				</div>
				<div class="layer ">
					<div>计划选择</div>
					<ul class=" plan-name">
						<li v-for="(item,index) in plans" :key="index" @click="changePlan(index)" :class="currentPlanIndex==index?'active':''">{{item.plan_name}}
						</li>
					</ul>
				</div>
				<div class="plan-con">
					<p class="ins">保障方案</p>
					<ul class="plans-con-wrapper">
						<li v-for="(plancon,index) in planCons" v-show=" index == currentPlanIndex" :key="index">
							<p class="item" v-for="(item,index) in plancon" :key="index">
								<span>{{item.name}}</span>
								<span>{{item.value}}</span>
							</p>
						</li>
					</ul>
				</div>
			</div>
			<div class="insured-pop" v-if="insuredShow" id="insured">
				<i class="close" @click="insuredShow=false"></i>
				<ul>
					<li class="clearfix" @click="seletcInsured(item)" v-for="(item,index) in insuredList" :key="index">
						<span class="name">{{item.name}}</span>
						<span class="relation">{{getRelation(item.type)}}</span>
						<span class="idnum">{{item.id_num}}</span>
					</li>
				</ul>
				<div class="add-btn-wrapper clearfix">
					<span @click="addInsuredShow=true">新增被保险人</span>
				</div>
			</div>

			<!--<insuredAdd-->
			<!--:isShow="addInsuredShow"-->
			<!--@cancel="addInsuredShow=false"-->
			<!--&gt;</insuredAdd>-->
		</div>
		<div class="cover" v-show="tbShow&&!timePickShow" @click="tbShow=false"></div>
		<detail-common @goInsured="submit" :price="totalPrice" :tbShow="tbShow"></detail-common>
	</div>
</template>

<script>
	import moment from 'moment'
	import {
		getRelation
	} from '@/assets/js/mixins/base.js'
	import detailCommon from '@/components/product/TheDetailCommon'
	import {
		getProductDetail
	} from '@/assets/js/mixins/productDetail.js'
	import {
		diffDate
	} from '@/assets/js/date'
	import {
		setLocalStorage,
		removeLocalStorage
	} from '@/assets/js/localStorage'
	export default {
		name: 'TheDetailCalculation',
		mixins: [getRelation, getProductDetail],
		components: {
			detailCommon
		},
		data() {
			return {
				timePickShow: false,
				startDate: moment(new Date()).add(1, 'days').format('YYYY-MM-DD'),
				endDate: moment(new Date()).add(1, 'year').format('YYYY-MM-DD'),
				insured: {
					age: '',
					sex: '',
					relationId: '',
					skuId: '',
					planId: ''
				},
				postInsured: {

				}
			}
		},

		methods: {
			submit() {
				if (!this.tbShow) {
					this.tbShow = true
					return false
				}
				/* if (!this.judgeCondition(this.insured.age)) return */
				this.$router.push({
					path: '/product/Occupy',
					query: {
						name: this.insuredName,
						productId: this.$route.query.productId
					}
				})
// 				this.$router.push({
// 					path: '/product/healthInfo',
// 					query: {
// 						name: this.insuredName,
// 						productId: this.$route.query.productId
// 					}
// 				})
				console.log('this.birthday', this.birthday, this.startDate, diffDate(this.birthday, this.startDate))
				this.insured.age = diffDate(this.birthday, this.startDate)
			},
			judgeCondition(age) {
				// 计划一、计划二  计划三、计划四  20-40周岁女性
				const ERROR_TIPS = '该产品计划仅限20周岁-40周岁的女性投保!'
				if (this.planIdArr[0] === this.plan_choice || this.planIdArr[1] === this.plan_choice || this.planIdArr[2] === this.plan_choice ||
					this.planIdArr[3] === this.plan_choice) {
					if (age.includes('天')) {
						this.$toast.center(ERROR_TIPS)
						return false
					}
					if (age.includes('岁')) {
						if (parseInt(age) < 20 || parseInt(age) > 40 || this.insured.sex == '男') {
							this.$toast.center(ERROR_TIPS)
							return false
						}
					}
					return true
				}
			},
			getCalculationFactors() {
				return new Promise((resolve, reject) => {
					let postData = {
						productId: this.$route.query.productId
					}
					this.$api.product.getCalculationFactors(postData).then(res => {
						if (res.code === 1) {
							resolve(res)
							this.calculationFactors = res.data
							for (let i = 0; i < res.data.length; i++) {
								if (res.data[i].is_default) {
									this.currentPlanIndex = i
									this.plan_choice = res.data[i].plan_id
									this.totalPrice = res.data[i].sku_price
								}
							}
							this.matchPrice(this.calculationFactors, this.insured.age, this.plan_choice)
						}
					})
				})
			},
			matchPrice(data, year, nameId) {
				console.log(678, data, year, nameId)
				data.forEach((item) => {
					console.log('year', year)
					console.log('item.calculation_factors[0].name', item.calculation_factors[0].name, item.calculation_factors[0].value)
					if (item.plan_id && item.plan_id === nameId &&
						item.calculation_factors && item.calculation_factors.length && item.calculation_factors[0].name && item.calculation_factors[
							0].value == '有' && this.checkYears(
							item.calculation_factors[1].value, year)) {
						this.totalPrice = item.sku_price
						console.log(123, this.totalPrice)
						this.insured.skuId = item.id
						this.insured.planId = item.plan_id
						return false
					}
				})
				return this.totalPrice
			},
			checkYears(val, year) {
				let arr = val.split('-')
				if (arr && arr.length) {
					if (arr[0] <= parseInt(year) && arr[1] >= parseInt(year)) {
						return true
					} else {
						return false
					}
				}
			}

		},
		watch: {
			plan_choice(val) {
				this.matchPrice(this.calculationFactors, this.insured.age, val)
			},
			'insured.age': (val) => {
				console.log('insured.age', val)
			}
		},
		beforeRouteLeave(to, from, next) {
			this.postInsured = {
				skuId: this.insured.skuId,
				planId: this.insured.planId,
				productId: this.$route.query.productId,
				relationId: this.insured.relationId,
				effDate: this.startDate,
				matuDate: this.endDate
			}
			//	if (to.name === 'Occupy') {
			setLocalStorage('postInsured', this.postInsured)
			//      } else {
			removeLocalStorage('insured')
			//	}
			next()
		}

	}
</script>

<style lang="scss">
	@import "@/assets/styles/productDetailCommon.scss";

	.v-modal {
		opacity: 1;
		background: rgba(28, 29, 29, 0.7) !important;
	}

	.product-detail .product-calculation .content .layer .plan-name {
		li {
			margin-bottom: pxToRem(12px);
		}

		li:nth-child(2n+2) {
			margin-right: 0;
		}

		li:nth-child(3),
		li:nth-child(4) {
			margin-bottom: 0;
		}
	}
</style>
