<template>
  <div class="detail-introduce">
    <div class="detail-tab ">
      <div class="tab-item" @click="setCur(0)" :class="{active:detailTabCur==0}">
        <span>保障解读</span>
      </div>
      <div class="tab-item" @click="setCur(1)" :class="{active:detailTabCur==1}">
        <span>投保须知</span>
      </div>
      <div class="tab-item" @click="setCur(2)" :class="{active:detailTabCur==2}">
        <span>常见问题</span>
      </div>
      <div class="tab-item" @click="setCur(3)" :class="{active:detailTabCur==3}">
        <span>理赔指引</span>
      </div>
    </div>
    <div class="tab-content">
      <div v-show="detailTabCur==0" class="tab-bzjd">
          <div v-for="(parameter,index)  of parameters" :key="index" class="banzhang-wrapper">
            <div class="group">
              {{parameter.group}}
            </div>
           <div v-for="(item,entryIdx)  of parameter.entries" :key="entryIdx"  >
              <div class="oneDiv" @click="setIdx(index,entryIdx)">
                <p>{{item.name}}</p>
                <p class="two">
                  {{item.limit}}  <i   :style="(idx[index] !== undefined && idx[index][entryIdx] !== undefined && idx[index][entryIdx]) ?  upImg : downImg"></i></p>
              </div>
              <div class="twoDiv"  >
                      <span class="text"   v-show="(idx[index] !== undefined && idx[index][entryIdx] !== undefined && idx[index][entryIdx])">{{item.value}}</span>
              <!--</div>-->
            </div>
           </div>
          </div>
      </div>
      <!--<div v-show="detailTabCur==1" v-html="introduce.introduction"></div>-->
	  <div v-show="detailTabCur==0" class="claimInfomation">
	  	查看&nbsp;<span class="link" @click="toClaim">《保险条款》</span>&nbsp;<span class="link" @click="toInfomation">《客户告知书》</span>&nbsp;<span class="link" @click="toImage">《保单样本书》</span>
	  </div>
      <div v-show="detailTabCur==1" v-html="introduce.insure_notice" class="tab-notice"></div>
      <div v-show="detailTabCur==2" class="tab-qa">
        <ul>
          <li v-for="(item,index) in introduce.interlocutions" :key="index">
            <p class="question "><i>Q</i><span>{{item.question}}</span></p>
            <p class="answer "><i>A</i><span>{{item.answer}}</span></p>
          </li>
        </ul>
      </div>
      <div v-show="detailTabCur==3" v-html="introduce.claim_guidelines" class="tab-claim"></div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'
import Sticky from '@/assets/plugins/sticky.js'
let Base64 = require('js-base64').Base64;

export default {
  name: 'TheDetailIntroduce',
  props: {
    introduce: {
      type: Object,
      default: () => {
        return {}
      }
    },
    parameters: {
      type: Array,
      defalut: []
    }
  },
  data () {
    return {
      idx: [],
      upImg: {
        'background-image':
        'url(' + require('@/assets/images/icon_up.png') + ')'
      },
      downImg: {
        'background-image':
        'url(' + require('@/assets/images/icon_down.png') + ')'
      }
    }
  },
  mounted () {
  },
  methods: {
    setIdx(index, idx) {
      let bool;
      let idxArray = [];
      if (this.idx[index] !== undefined) {
        idxArray = this.idx[index];
      }
      if (this.idx[index] === undefined || !this.idx[index].hasOwnProperty(idx)) {
        bool = true;
        idxArray[idx] = bool;
      } else {
        bool = !this.idx[index][idx];
        idxArray[idx] = bool;
      }
      this.$set(this.idx, index, idxArray);
    },
    setCur (index) {
      this.SET_DETAIL_TAB_CUR(index)
    },
    ...mapMutations([
      'SET_DETAIL_TAB_CUR'
    ]),
	toClaim(){
		this.$router.push('/claimNotification/'+Base64.encode(this.$route.query.productId));
	},
	toInfomation(){
		this.$router.push('/customerNotification/'+Base64.encode(this.$route.query.productId));
	},
	toImage(){
		window.location.href =  process.env.VUE_APP_BASE_URL + this.introduce.policy_sample_url;
	}
  },
  directives: {
    Sticky
  },
  beforeDestroy () {
    this.SET_DETAIL_TAB_CUR(0)
  },
  computed: {
    ...mapGetters([
      'detailTabCur'
    ])
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";
  .detail-introduce {
    .detail-tab {
      background: #fff;
      display: flex;
      width: 100%;
      border-bottom: $border-bottom-light-color;
      box-sizing: border-box;
      .tab-item {
        width: 25%;
        height: pxToRem(80px);
        line-height: pxToRem(80px);
        font-size: pxToRem(28px);
        color: $color-text;
        text-align: center;
        &.active {
          background: #fff;
          border-bottom: pxToRem(4px) solid $color-active-text;
          box-sizing: border-box;
        }
      }
    }
    .tab-content {
      background: #fff;
	  .claimInfomation{
		  font-size: pxToRem(28px);
		  line-height: pxToRem(60px);
		  padding-left: pxToRem(30px);
		  .link{
			color: #2098ee;  
		  }
	  }
      .tab-bzjd {
        padding-left: pxToRem(30px);;
        .banzhang-wrapper {
          .group{
            position: relative;
            font-size:pxToRem(24px);
            padding:pxToRem(36px) 0 pxToRem(7px);
            color: #A5A8A8;
            padding-left: pxToRem(32px);
            &:before{
              content: '';
              position: absolute;
              top: pxToRem(46px);
              left:0;
              display: block;
              width:pxToRem(24px);
              height:pxToRem(4px);
              background: $color-active-text;
            }
          }
          .oneDiv{
            display: flex;
            justify-content: space-between;
            height:pxToRem(84px);
            line-height: pxToRem(84px);
            border-bottom: $border-bottom-light-color;
            .two{
              display: flex;
              justify-content: space-between;
              align-items: center;
              padding-right:pxToRem(20px);
              i{

                display: block;
                width:pxToRem(44px);
                height: pxToRem(44px);
                background-position: right center;
                background-size: pxToRem(44px) pxToRem(44px);
              }
            }
          }
          .twoDiv{
            .text{
              display: inline-block;
              font-size: pxToRem(24px);
              color: #A5A8A8;
              line-height: pxToRem(38px);
               border-bottom: $border-bottom-light-color;
              padding:pxToRem(12px) pxToRem(30px) pxToRem(12px) 0;
            }

          }
          &:first-child{
            .group{
              padding-top:pxToRem(30px);
              &:before{
                top: pxToRem(42px);
              }
            }
          }
        }

      }
      .tab-notice{
         padding: pxToRem(21px) pxToRem(34px) pxToRem(30px) pxToRem(30px);
        overflow-x: hidden;
        line-height: pxToRem(44px) !important;
        word-wrap: break-word;
        word-break: normal;
      }
      .tab-qa {
        color: $color-text;
        font-size: 0;
        font-size: pxToRem(28px);
        line-height: pxToRem(44px);
        padding: pxToRem(26px) pxToRem(28px) pxToRem(30px) pxToRem(32px);
        i {
          display: block;
          width: pxToRem(36px);
          height: pxToRem(36px);
          line-height: pxToRem(36px);
          border-radius: 50%;
          color: #FFFFFF;
          text-align: center;
          margin-right: pxToRem(15px);
          margin-top: pxToRem(4px);
          font-size:pxToRem(24px);
        }
        span {
          flex: 1;
          font-size: pxToRem(28px);
          color: $color-text;
        }
        .question {
          font-weight: bold;
          margin-bottom: pxToRem(16px);
          display: flex;
          i {
            background: $color-active-text
          }
        }
        .answer {
          display: flex;
          margin-bottom:  pxToRem(48px);
          i {
            background: #D5D8D8;
          }
        }
      }
      .tab-claim{
        padding: pxToRem(21px) pxToRem(30px);
      }
    }
  }

</style>
